  display: flex;
  min-width: 35ch;
  margin: 10px;
  box-shadow: 2px 3px 10px 2px #D7DFFF;
  height: 60%;
  min-height: 30vh;
  flex-wrap: wrap;
  flex-direction: column;
  border-radius: 10px;
  padding: 10px;
}

.drop-zone .drag-el-horizontal:first-of-type {
  background-color: #6D71FA;
  color: white;
}

